/**
 * Block Editor
 *
 * The following styles get applied inside the editor only.
 */
.draft-block {
  opacity: .4; }
  .draft-block__selected {
    opacity: .6; }

.wholesome-publishing-comments__panel .comment {
  background: #f3f4f5;
  margin-bottom: 16px;
  padding: 8px; }

.wholesome-publishing-comments__panel .comment__header {
  align-items: flex-start;
  display: flex; }

.wholesome-publishing-comments__panel .comment__avatar {
  margin-right: 8px;
  max-width: 28px; }

.wholesome-publishing-comments__panel .comment__username {
  margin: 0; }

.wholesome-publishing-comments__panel .comment__meta {
  flex: 1; }

.wholesome-publishing-comments__panel .comment__body {
  margin-bottom: 8px;
  margin-left: 36px;
  margin-top: 4px; }

.wholesome-publishing-comments__panel .comment__comment {
  white-space: pre-line;
  width: 100%;
  word-break: break-all; }

.wholesome-publishing-comments__panel .comment.comment--child {
  background: #dee0e1;
  margin-left: 36px;
  margin-top: -4px;
  padding: 8px; }
  .wholesome-publishing-comments__panel .comment.comment--child .comment__body {
    margin-left: 0; }

.wholesome-publishing-comments__panel .comment__controls {
  display: flex;
  flex-direction: row-reverse;
  margin-left: 36px; }
